.modal{
    position: fixed;
    z-index: 999;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.75);
    display: none;
    justify-content: center;
    align-items: center;
}

.active{
    display: flex;
}

.no-active{
    display: none;
    opacity: 0;
}

.modal-container{
    position: relative;
    padding: 1rem;
    min-width: 320px;
    max-width:480px;
    min-height: 200px;
    max-height: 600px;
    overflow-y: auto;
    border-radius: 3px;
    background-color: rgb(19, 19, 19);
}

.modal-container h3{
    font-size: 18px;
    color: rgb(255, 255, 255);
    text-transform: uppercase;
    letter-spacing:3px;
    text-align: center;
}

.modal-close{
    position: absolute;
    border: 0;
    background-color: transparent;
    font-size: 30px;
    color: rgb(57, 221, 243);
    top: 1rem;
    right: 1rem;
    cursor: pointer;
    font-weight: 700;
}